// original: users/views.py -> home view (renders events list typically)
// Home page - shows welcome or redirects to dashboard

import { useEffect } from 'react';
import { useRouter } from 'next/router';
import { useAuth } from '../context/AuthContext';
import Layout from '../components/Layout';
import Link from 'next/link';

export default function Home() {
  const router = useRouter();
  const { isAuthenticated, loading } = useAuth();

  useEffect(() => {
    if (!loading && isAuthenticated) {
      router.push('/dashboard');
    }
  }, [isAuthenticated, loading, router]);

  if (loading) {
    return (
      <Layout title="CampusNexus - Campus Event Management">
        <div className="text-center" style={{ paddingTop: '100px' }}>
          <div className="spinner-border text-warning" role="status">
            <span className="visually-hidden">Loading...</span>
          </div>
        </div>
      </Layout>
    );
  }

  return (
    <Layout title="CampusNexus - Campus Event Management">
      <div className="row justify-content-center" style={{ paddingTop: '50px' }}>
        <div className="col-md-8 text-center">
          <h1 style={{ fontSize: '3rem', marginBottom: '2rem' }}>
            Welcome to <span style={{ color: 'var(--orange)' }}>CampusNexus</span>
          </h1>
          <p style={{ fontSize: '1.2rem', marginBottom: '3rem' }}>
            Your all-in-one platform for discovering, organizing, and managing campus events.
            Connect with your college community and never miss an event!
          </p>
          
          <div className="row mb-5">
            <div className="col-md-4 mb-3">
              <div className="card">
                <div className="card-body">
                  <h3>üìÖ</h3>
                  <h5>Discover Events</h5>
                  <p>Browse upcoming events and find what interests you</p>
                </div>
              </div>
            </div>
            <div className="col-md-4 mb-3">
              <div className="card">
                <div className="card-body">
                  <h3>üéØ</h3>
                  <h5>Easy Registration</h5>
                  <p>Register for events with a single click</p>
                </div>
              </div>
            </div>
            <div className="col-md-4 mb-3">
              <div className="card">
                <div className="card-body">
                  <h3>üèÜ</h3>
                  <h5>Earn Points</h5>
                  <p>Participate, provide feedback, and climb the leaderboard</p>
                </div>
              </div>
            </div>
          </div>

          <div>
            <Link href="/auth/login" className="btn btn-primary btn-lg me-3">
              Login
            </Link>
            <Link href="/auth/signup" className="btn btn-secondary btn-lg">
              Sign Up
            </Link>
          </div>
        </div>
      </div>
    </Layout>
  );
}
